import {
  getToken,
  setToken,
  removeToken,
  storages,
  setLocalStorage,
  getLocalStorage
} from "@/utils/auth";
import mainHttp from "@/api/index";

const user = {
  state: {
    token: getToken(),

    userId: getLocalStorage(storages.userId),
    userName: getLocalStorage(storages.userName),
    userPassword: getLocalStorage(storages.userPassword),
    serverIP: getLocalStorage(storages.serverIP),

    student: getLocalStorage(storages.student, true),
    studentID: getLocalStorage(storages.StudentID)
  },
  mutations: {
    SET_TOKEN: (state, value) => {
      state.token = value;
      setToken(value);
    },
    SET_KEY: (state, value) => {
      state.userId = value;
      setLocalStorage(storages.userId, value, 1000 * 60 * 60 * 12);
    },
    SET_SERVER_IP: (state, value) => {
      state.serverIP = value;
      setLocalStorage(storages.serverIP, value);
    },

    SET_USER: (state, value) => {
      state.userName = value;
      setLocalStorage(storages.userName, value);
    },
    SET_USER_PASSWORD: (state, value) => {
      state.userPassword = value;
      setLocalStorage(storages.userPassword, value);
    },
    SET_STUDENT: (state, value) => {
      state.student = value;
      setLocalStorage(storages.student, value);
    },
    SET_STUDENT_ID: (state, value) => {
      state.studentID = value;
      setLocalStorage(storages.StudentID, value);
    }
  },

  actions: {
    /**
     * 登录请求
     * @param {*} datas {userInfo, other}
     * @returns {Promise<any>}
     */
    LoginByUser({ commit, state }, datas) {
      return new Promise((resolve, reject) => {
        mainHttp.user.postLogin(
          datas.userInfo,
          datas.other
        ).then(res => {
          if (res.Result) {
            if (state.userName !== res.UserInfo.UserName) {
              commit("SET_NOW_COURSE", null);
            }
            commit("SET_TOKEN", res.Data);
            commit("SET_KEY", res.UserInfo.UserID);
            commit("SET_USER", res.UserInfo.UserName);
            commit("SET_STUDENT", res.Student);
            commit("SET_STUDENT_ID", res.Student ? res.Student["StudentID"] : "");
          }
          resolve(res);
        }).catch(error => {
          reject(error);
        });
      });
    },
    // user logout
    Logout({ commit, state }) {
      return new Promise((resolve, reject) => {
        commit("SET_KEY", "");
        commit("SET_STUDENT", null);
        commit("SET_STUDENT_ID", "");
        commit("SET_TOKEN", null);
        removeToken();
        resolve();
      });
    }
  }
};

export default user;



