<template>
  <div id="foot-bar">
    <van-tabbar @change="tgTab" v-model="FootActive">
      <!-- 暂无数据 隐藏 -->
      <van-tabbar-item to="/home-page" replace>
        <img
          slot="icon"
          slot-scope="props"
          :src="
            props.active
              ? require('../assets/icon_sy_pressed.png')
              : require('../assets/icon_sy_normal.png')
          "
        />
        <span>首页</span>
      </van-tabbar-item>
      <van-tabbar-item to="/home-source" replace v-show="true">
        <van-icon slot="icon" size="22" class-prefix="iconfont icon-weike"/>
        <span>资源</span>
      </van-tabbar-item>
      <van-tabbar-item to="/home-course" replace>
        <img
          slot="icon"
          slot-scope="props"
          :src="
            props.active
              ? require('../assets/icon_kc_pressed.png')
              : require('../assets/icon_kc_normal.png')
          "
        />
        <span>课程</span>
      </van-tabbar-item>
      <van-tabbar-item to="/my-zone" replace>
        <img
          slot="icon"
          slot-scope="props"
          :src="
            props.active
              ? require('../assets/icon_wd_pressed.png')
              : require('../assets/icon_wd_normal.png')
          "
        />
        <span>我的</span>
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { Tabbar, TabbarItem } from "vant";
export default {
  name: "",
  components: { [Tabbar.name]: Tabbar, [TabbarItem.name]: TabbarItem },
  props: {},
  data() {
    return {
      FootActive: ""
    };
  },
  computed: {},
  watch: {
    "$route.path"(newVal, oldVal) {
      if (newVal === "/home-page") {
        this.FootActive = 0;
      } else if (newVal === "/home-source") {
        this.FootActive = 1;
      } else if (newVal === "/home-course") {
        this.FootActive = 2;
      } else if (newVal === "/my-zone") {
        this.FootActive = 3;
      }
    }
  },
  created() {},
  mounted() {
    let path = this.$route.path;
    if (path === "/home-page") {
      this.FootActive = 0;
    } else if (path === "/home-source") {
      this.FootActive = 1;
    } else if (path === "/home-course") {
      this.FootActive = 2;
    } else if (path === "/my-zone") {
      this.FootActive = 3;
    }
  },
  methods: {
    tgTab(val) {
      // console.log(val);
    }
  }
};
</script>

<style lang="less">
#foot-bar {
  z-index: 998;
  width: 100%;
  height: 50px;
  position: fixed;
  bottom: 0;
  overflow: hidden;
  .van-tabbar-item--active {
    color: #00cbab;
  }
}
</style>



