import { httpGet, basePath } from "./../request";

const Gateway = `${basePath}/I_GatewayService/`;
export default {
  getThrowScreen: data => {
    return httpGet(`${Gateway}GetStudentThrowScreen`, data);
  },
  getScreenInfo: data => {
    return httpGet(`${Gateway}GetDataByScreenCode`, data);
  },
  getScreenState: data => {
    return httpGet(`${Gateway}GetThrowScreenState`, data);
  },
  getScreenConfig: data => {
    return httpGet(`${Gateway}GetThrowScreenInfo`, data);
  }
};



