import { mentalUrl, httpUrl, basePath, httpGet, httpPost } from "./../request";

export default {
  downloadURL: () => {
    return `${httpUrl("Download")}`;
  },
  downloadPhoto: (userID, personID, fileType = 20) => {
    return `${httpUrl("DownloadPhoto")}?userID=${userID}&photoID=${personID}&fileType=${fileType}`;
  },
  picture: (id, type) => {
    return httpUrl(
      `InteractDownload?fileName=${id}&fileType=${type}&platFormType=1`
    );
  },
  video: (id, fileType) => {
    return mentalUrl(`${id}.${fileType}`);
  },
  file: id => {
    return httpUrl(`DownloadFileByMID?mid=${id}`);
  },
  preview: id => {
    return httpUrl(`PreviewFileByMID?mid=${id}`);
  },
  upload: data => {
    return httpPost(`${basePath}/InteractUploadFile`, data);
  },
  pictureUpload: data => {
    return httpPost(`${basePath}/UploadImageByBaseString`, data);
  },
  viewFile: data => {
    return httpGet(`${basePath}/PreviewFile`, data, {
      responseType: "arraybuffer",
      headers: {
        common: {
          "Access-Platform": "APP"
        }
      }
    });
  },
  viewTxt: data => {
    return httpGet(`${basePath}/PreviewTxt`, data);
  },
  serverConfigInfoGet: data => {
    return httpGet(`${basePath}/GetConfigInfo`, data);
  }
};



