import { getNowDateTime } from "@/utils/index";
import { ModulePicture } from "@/utils/enum";

export default {
  data () {
    return {};
  },
  methods: {
    isAutoNote () {
      window.uploadImage = imgFile => {
        this.receiveNote(imgFile);
      };
    },
    // notAutoNote() {
    //   window.uploadImage = imgFile => {
    //     this.afterRead(imgFile);
    //   };
    // },
    receiveNote (file) {
      // 防止其它页面做笔记，导致异常
      if (this.$route.matched.findIndex(r => r.path === "/courseIndex") > -1) {
        if (!this.$store.getters.courseID || !this.$store.getters.studentID) {
          return;
        }
        console.log("开始保存笔记:" + file);

        if (file) {
          const reqData = {
            funType: ModulePicture.StudentNote,
            imgBaseString: file
          };
          this.$mainHttp.common.pictureUpload(reqData).then(res => {
            if (res.Result) {
              this.saveNote(res.Data);
            }
          }).catch(() => {
            this.$toast("添加笔记失败");
          });
        }
      }
    },
    saveNote (imgId) {
      this.$mainHttp.note.postSave({
        snID: "",
        ciID: this.$store.getters.courseID,
        chID: this.$store.getters.course["CHID"],
        studentID: this.$store.getters.studentID,
        title: getNowDateTime(),
        content: `<img src="picture/${imgId}/${ModulePicture.StudentNote}" alt="" />`
      }).then(res => {
        if (res.Result) {
          this.$toast.success("新增笔记成功");
          if (this.$route.path.indexOf("/note") === -1) {
            this.$router.push("/note");
          } else {
            this.$store.commit("SET_NOTE_REFRESH", true);
          }
        }
      }).catch(() => {
        this.$toast("添加笔记失败");
      });
    }
  },
  watch: {
    $route (newVal, oldVal) {
      if (newVal && newVal.meta && newVal.meta.unAutoNote) {
        // this.notAutoNote();
      } else if (newVal.matched.findIndex(r => r.path === "/courseIndex") > -1) {
        this.isAutoNote();
      }
    }
  }
};



