import { httpGet, httpPost, basePath } from "./../request";

const ScreenQuestion = `${basePath}/I_ScreenSubjectService/`;
export default {
    getListByType: data => {
        return httpGet(
            `${ScreenQuestion}GetGroupByTypeStats`,
            data
        );
    },
    getList: data => {
        return httpGet(
            `${ScreenQuestion}GetDatas`,
            data
        );
    },
    getData: data => {
        return httpGet(
            `${ScreenQuestion}GetSSAndSA`,
            data
        );
    },
    // 提交答案
    postAnswer: data => {
        return httpPost(
            `${ScreenQuestion}SubmitAnswer`,
            data
        );
    },
    // 提交问卷调查
    postSurvey: data => {
        return httpPost(
            `${ScreenQuestion}SubmitSurvey`,
            data
        );
    },
    // 提交抢答
    postResponder: data => {
        return httpPost(
            `${ScreenQuestion}StartResponder`,
            data
        );
    }
};


