const courseSourceRouter = [
  {
    path: "/source/classification",
    name: "sourceClassification",
    meta: {
      title: "分类列表",
      from: "/home-source",
      cache: true
    },
    component: () => import("@/pages/source/classification/index.vue")
  },
  {
    path: "/source/classification-detail",
    name: "ClassificationDetail",
    meta: { title: "分类结果", from: "", cache: true },
    component: () => import("@/pages/source/classification/Detail.vue")
  },
  {
    path: "/source/lesson-detail",
    name: "lessonDetail",
    meta: { title: "课单详情", from: "", cache: true },
    component: () => import("@/pages/source/LessonOne.vue")
  },
  {
    path: "/source/recorded",
    name: "sourceRecorded",
    meta: { title: "录播播放", from: "", cache: true, noLoading: true },
    component: () => import("@/pages/source/course-vedio/recorded.vue")
  },
  {
    path: "/source/live",
    name: "sourceLive",
    meta: { title: "直播", from: "", cache: true, noLoading: true },
    component: () => import("@/pages/source/course-vedio/live.vue")
  },
  {
    path: "/source/play-record",
    name: "sourcePlayRecord",
    meta: { title: "播放记录", from: "", cache: true },
    component: () => import("@/pages/source/play-record/index.vue")
  },
  {
    path: "/source/vedio-list",
    name: "sourceVedioList",
    meta: { title: "视频列表", from: "", cache: true },
    component: () => import("@/pages/source/course-vedio/list.vue")
  }
];

export default courseSourceRouter;



