import mingle from "./mingle";
const main = require.context("./main", false, /\.js$/);
const interact = require.context("./interact", false, /\.js$/);
// console.log("context:", context.keys());
const api = { ...mingle };

main.keys().forEach(fileName => {
    // 获取组件的 PascalCase 命名
    const componentName = fileName.split('/').pop().replace(/\.\w+$/, '')
    // console.log("key:", fileName, context(fileName), context(fileName).default, componentName);
    api[componentName] = main(fileName).default;
});

interact.keys().forEach(fileName => {
    const componentName = fileName.split('/').pop().replace(/\.\w+$/, '')
    api[componentName] = interact(fileName).default;
});
// console.log(api);
export default api;


