import { httpPost, basePath } from "./../request";

const Class = `${basePath}/I_ClassService/`;
export default {
  joinClass: data => {
    return httpPost(
      `${Class}JoinClass`,
      data
    );
  },
  postJoinClass: data => {
    return httpPost(
      `${Class}StuJoinClass`,
      data
    );
  },
  postEnterClass: data => {
    return httpPost(
      `${Class}EnterClass`,
      data
    );
  },
  photoUpload: data => {
    return httpPost(`${Class}/UploadPhoto`, data);
  }
};



