import { default as service, basePath } from "./request";

export default {
  ElaborateCourses: {
    ServiceURL: `${basePath}/ElaborateCoursesService/`,
    async GetECDatas (data) {
      const res = await service({
        url: `${this.ServiceURL}GetECDatas`,
        method: "get",
        params: data
      });
      if (res) {
        return res;
      }
    },
    /**
     * 根据id获取发布信息
     * @param {*} ecid          课程编号
     * @param {*} updateVisits  是否更新访问次数
     */
    async GetData (data) {
      const res = await service({
        url: `${this.ServiceURL}GetData`,
        method: "get",
        params: data
      });
      if (res) {
        return res;
      }
    },
    /* 获取相关资源
     * @param {*} key         查询关键字
     * @param {*} type        类型0: 老师名称 1: 专业名称 2：课程名称
     * ecid, key, type
     */
    async GetRelevantDatas (data) {
      const res = await service({
        url: `${this.ServiceURL}GetRelevantDatas`,
        method: "get",
        params: data
      });
      if (res) {
        return res;
      }
    },
    /**
     * 获取评论列表
     * @param {*} ecid  精品课程编号
     * @param {*} psize 每页显示几条数据
     * @param {*} pno   当前第几页
     */
    async GetComments (data) {
      const res = await service({
        url: `${this.ServiceURL}GetComments`,
        method: "get",
        params: data
      });
      if (res) {
        return res;
      }
    },
    /**
     * 删除评论
     * @param {*} eccid      评论编号
     */
    async DelComment (data) {
      const res = await service({
        url: `${this.ServiceURL}DelComment`,
        method: "post",
        data: data
      });
      if (res) {
        return res;
      }
    },
    /**
     * 添加评论
     * @param {*} ecid      精品课程编号
     * @param {*} content   评价内容
     */
    async AddComment (data) {
      const res = await service({
        url: `${this.ServiceURL}AddComment`,
        method: "post",
        data: data
      });
      if (res) {
        return res;
      }
    },
    /**
     * 添加收藏
     * @param {*} ecid      精品课程编号
     */
    async AddFav (data) {
      const res = await service({
        url: `${this.ServiceURL}AddFav`,
        method: "post",
        data: data
      });
      if (res) {
        return res;
      }
    },
    /**
     * 删除收藏
     * @param {*} ecfid      收藏编号
     */
    async DelFav (data) {
      const res = await service({
        url: `${this.ServiceURL}DelFav`,
        method: "post",
        data: data
      });
      if (res) {
        return res;
      }
    },
    /**
     * 获取最近看过列表
     * @param {*} psize 每页显示几条数据
     * @param {*} pno   当前第几页
     */
    async GetLatelyDatas (data) {
      const res = await service({
        url: `${this.ServiceURL}GetLatelyDatas`,
        method: "get",
        params: data
      });
      if (res) {
        return res;
      }
    },
    /**
     * 获取收藏列表
     * @param {*} psize 每页显示几条数据
     * @param {*} pno   当前第几页
     */
    async GetFavs (data) {
      const res = await service({
        url: `${this.ServiceURL}GetFavs`,
        method: "get",
        params: data
      });
      if (res) {
        return res;
      }
    }
  },
  RBConfig: {
    ServiceURL: `${basePath}/RBConfigService/`,
    async GetLiving (data) {
      const res = await service({
        url: `${this.ServiceURL}GetLiving`,
        method: "get",
        params: data
      });
      if (res) {
        return res;
      }
    }
  },
  LiveTask: {
    ServiceURL: `${basePath}/LiveTaskService/`,
    async GetData (data) {
      const res = await service({
        url: `${this.ServiceURL}GetData`,
        method: "get",
        params: data
      });
      if (res) {
        return res;
      }
    },
    /**
     * 获取即将开始的直播
     */
    async GetEvalTasks () {
      const res = await service({
        url: `${this.ServiceURL}GetEvalTasks`,
        method: "get"
      });
      if (res) {
        return res;
      }
    },
    /**
     * 获取直播计划
     * param:start, end, pno, psize
     */
    async GetLivePlan (data) {
      const res = await service({
        url: `${this.ServiceURL}GetLivePlan`,
        method: "get",
        params: data
      });
      if (res) {
        return res;
      }
    },
    /**
     * 获取已完成的直播
     *  param:start, end, pno, psize
     */
    async GetOverLive (data) {
      const res = await service({
        url: `${this.ServiceURL}GetOverLive`,
        method: "get",
        params: data
      });

      if (res) {
        return res;
      }
    },
    // param:lid
    async GetEvalTaskInfo (data) {
      const res = await service({
        url: `${this.ServiceURL}GetEvalTaskInfo`,
        method: "get",
        params: data
      });

      if (res) {
        return res;
      }
    },
    /**
     * 添加订阅 iveTaskId, livePlanId
     */
    async AddLiveRemind (data) {
      const res = await service({
        url: `${this.ServiceURL}AddLiveRemind`,
        method: "post",
        data: data
      });
      if (res) {
        return res;
      }
    },
    /**
     * 取消订阅lrid: lrid
     */
    async DelLiveRemind (data) {
      const res = await service({
        url: `${this.ServiceURL}DelLiveRemind`,
        method: "post",
        data: data
      });
      if (res) {
        return res;
      }
    }
  },
  Metadata: {
    ServiceURL: `${basePath}/MetadataService/`,
    async GetTrees (data) {
      const res = await service({
        url: `${this.ServiceURL}GetTrees`,
        method: "get",
        params: data
      });

      if (res) {
        return res;
      }
    }
  }
};



