import { httpGet, httpPost, basePath } from "./../request";

const Question = `${basePath}/I_QuestionService/`;
export default {
    getList: data => {
        return httpGet(
            `${Question}GetDatas`,
            data
        );
    },
    postDataSave: data => {
        return httpPost(
            `${Question}Add`,
            data
        );
    }
};


