<template>
  <div id="index">
    <transition :name="transition">
      <router-view class="router-view"></router-view>
    </transition>
    <foot-bar></foot-bar>
  </div>
</template>

<script>
import FootBar from "@/components/FootBar";

export default {
  name: "",
  components: { FootBar },
  props: {},
  data() {
    return {};
  },
  computed: {
    transition() {
      if (!this.$store.routerDirection) return 'slide-normal'
      return `slide-${this.$store.routerDirection}`
    }
  }
};
</script>

<style lang='less'>
#index {
  width: 100%;
  min-height: 100%;
  background: #ebf0ef;
  position: relative;
}
</style>


