import { FuncType, sendToAPP } from "@/utils/interFlow";

export default {
  data () {
    return {
      isLog: true
    };
  },
  beforeCreate () {
    // 根据投屏码获取IP
    window.getScreenIP = code => {
      this.getScreenInfo(code);
      return true;
    };
    // 获取当前的投屏状态
    window.getScreenStatus = () => {
      this.getScreenState();
      return true;
    };
    // H5跳转链接
    window.onLoad = value => {
      this.$router.push(value);
      return true;
    };
    // 获取学生姓名(需登录后获取)
    window.getStudentName = () => {
      return this.$store.getters.student["StudentName"] || "";
    };
    // 发送当前IP给H5(一般启动之后发送)
    window.receiveIP = value => {
      if (value) {
        this.$store.commit("SET_IP", value);
        if (value !== this.$store.getters.localIP) this.$store.commit("SET_REGETKEY", true);
        return true;
      } else {
        return false;
      }
    };
    // 学生投屏状态 更新(传递给服务端)
    window.setActiveScreenIp = value => {
      this.$mainHttp.gateway.getThrowScreen({
        ciID: this.$store.getters.courseID,
        state: value ? "1" : "0",
        jsIP: value,
        fsIP: this.$store.getters.localIP || ""
      }).then(res => {
        if (res.Result) {

        }
      }).catch(() => {

      });
      return true;
    };
  },
  mounted () {
    this.$nextTick(() => {
    });
  },
  methods: {
    getScreenInfo (code) {
      // return new Promise((resolve, reject) => {
      this.$mainHttp.gateway.getScreenInfo({
        screenCode: code
      }).then(res => {
        if (res.Result && res.Data && res.Data["GatewayIP"]) {
          // resolve(res.Data["GatewayIP"]);
          sendToAPP(FuncType.searchIP, res.Data["GatewayIP"]);
        } else {
          sendToAPP(FuncType.searchIP, "");
        }
      }).catch(() => {
        sendToAPP(FuncType.searchIP, "");
      });
      // });
    },
    getScreenState () {
      this.$mainHttp.gateway.getScreenState({
        ciID: this.$store.getters.courseID
      }).then(res => {
        sendToAPP(FuncType.searchScreenState, res ? "1" : "0");
      }).catch(() => {
        sendToAPP(FuncType.searchScreenState, "");
      });
    },
    getNowDateTime () {
      let date = new Date();
      let Y = date.getFullYear() + "-";
      let M = (date.getMonth() + 1 < 10 ? "0" + (date.getMonth() + 1) : date.getMonth() + 1) + "-";
      let D = (date.getDate() < 10 ? "0" + date.getDate() : date.getDate()) + " ";

      let h = (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) + ":";
      let m = (date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes()) + ":";
      let s = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
      return Y + M + D + h + m + s;
    },

    // sendToAPP(funcName, value) {
    //   console.log("funcName:", funcName);
    //   if (!window.Android) return;
    //   this["countSend" + funcName] = 0;
    //   this["timer" + funcName] = setInterval(() => {
    //     this["countSend" + funcName]++;
    //     let state = false;
    //     try {
    //       state = window.Android[funcName](value);
    //     } catch (err) {}

    //     if (state || this["countSend" + funcName] >= 10) {
    //       if (this["timer" + funcName])
    //         window.clearInterval(this["timer" + funcName]);
    //     }
    //   }, 500 * 1);
    // },

    clearInstance () {
      if (this.timer) {
        window.clearInterval(this.timer);
      }
    }
  },
  watch: {},
  destroyed () {
    // this.clearInstance();
    // if (this.timerSignalR) {
    //   window.clearInterval(this.timerSignalR);
    // }
    // if (this.timerRefreshScreenCode) {
    //   window.clearInterval(this.timerRefreshScreenCode);
    // }
    // if (this.timerSendToPath) {
    //   window.clearInterval(this.timerSendToPath);
    // }
    // if (this.timerSendBoardCast) window.clearInterval(this.timerSendBoardCast);
    // if (this.timerSendThrowScreen)
    //   window.clearInterval(this.timerSendThrowScreen);
  }
};



