import { storages, setLocalStorage, getLocalStorage } from "@/utils/auth";
import mainHttp from "@/api/index";

const state = {
  loading: false, // 页面加载状态
  requestNum: 0, // 当前请求数量

  serverConfig: getLocalStorage(storages.serverConfig),
  // signalR服务
  interactServer: getLocalStorage(storages.interactServer),
  interactPort: getLocalStorage(storages.interactPort),

  singleLoginReal: 0,
  singleLoginType: getLocalStorage(storages.singleLoginType),
  singleLoginUrl: getLocalStorage(storages.singleLoginUrl),
  singleLogOutUrl: getLocalStorage(storages.singleLogOutUrl),

  deviceID: getLocalStorage(storages.deviceID),
  localIP: getLocalStorage(storages.localIP),

  agreeUse: getLocalStorage(storages.agreeUse),
  routerDirection: getLocalStorage(storages.routerDirection),
  cacheComponents: ["homePage", "homeSource", "homeCourse", "myZone"],
  navigations: getLocalStorage(storages.navigations) || [],
  imagePreview: ""
};

const mutations = {
  SET_LOADING: (state, value) => {
    state.loading = value;
  },
  SET_REQUEST: (state, value) => {
    state.requestNum += value;
  },

  SET_SERVER_CONFIG: (state, value) => {
    state.serverConfig = value;
    setLocalStorage(storages.serverConfig, value);
  },
  SET_INTERACT_SERVER: (state, value) => {
    state.interactServer = value;
    setLocalStorage(storages.interactServer, value);
  },
  SET_INTERACT_PORT: (state, value) => {
    state.interactPort = value;
    setLocalStorage(storages.interactPort, value);
  },

  SET_SINGLE_LOGIN_REAL: (state, value) => {
    state.singleLoginReal = value;
    setLocalStorage(storages.singleLoginReal, value);
  },
  SET_SINGLE_LOGIN_TYPE: (state, value) => {
    state.singleLoginType = value;
    setLocalStorage(storages.singleLoginType, value);
  },
  SET_SINGLE_LOGIN_URL: (state, value) => {
    state.singleLoginUrl = value;
    setLocalStorage(storages.singleLoginUrl, value);
  },
  SET_SINGLE_LOG_OUT_URL: (state, value) => {
    state.singleLogOutUrl = value;
    setLocalStorage(storages.singleLogOutUrl, value);
  },

  SET_DEVICE_ID: (state, id) => {
    state.deviceID = id;
    setLocalStorage(storages.deviceID, id);
  },
  SET_IP: (state, ip) => {
    state.localIP = ip;
    setLocalStorage(storages.localIP, ip);
  },

  SET_AGREE_USE: (state, value) => {
    state.agreeUse = value;
    setLocalStorage(storages.agreeUse, value);
  },

  SET_ROUTER_DIRECTION: (state, value) => {
    state.routerDirection = value;
    setLocalStorage(storages.routerDirection, value);
  },
  SET_NAVIGATIONS: (state, value) => {
    state.navigations = [];
    state.navigations = value.slice(0, value.length);
    setLocalStorage(storages.navigations, value);
  },

  SET_IMAGE_PREVIEW: (state, value) => {
    state.imagePreview = value;
  },

  SET_CACHE_COMPONENTS: (state, value) => {
    state.cacheComponents = value;
  }
};

const actions = {
  /**
   * 获取服务器的配置参数
   * @param commit
   * @param state
   * @param getters
   */
  ServerConfigGet ({ commit, state, getters }) {
    return new Promise((resolve, reject) => {
      if (getters.serverIP) {
        mainHttp.common
          .serverConfigInfoGet()
          .then(res => {
            if (res.Result && res.Datas) {
              // 互动教室配置
              commit("SET_SERVER_CONFIG", res.Datas);
              if (
                state.interactServer !== res.Datas["InteractTCPIP"] ||
                state.interactPort !== res.Datas["InteractIMPort"]
              ) {
                commit("SET_INTERACT_SERVER", res.Datas["InteractTCPIP"]);
                commit("SET_INTERACT_PORT", res.Datas["InteractIMPort"]);
                // commit("SET_RE_GET_KEY", true);
              }

              // 统一登录
              commit("SET_SINGLE_LOGIN_TYPE", res.Datas["UnifiedLogin"]);
              if (state.singleLoginType + "" === "2") {
                commit("SET_SINGLE_LOGIN_URL", res.Datas["UnifiedloginURL"]);
                commit(
                  "SET_SINGLE_LOG_OUT_URL",
                  res.Datas["UnifiedloginOutUrl"]
                );
              } else {
                commit("SET_SINGLE_LOGIN_URL", "");
                commit("SET_SINGLE_LOG_OUT_URL", "");
              }
            }
            resolve(res);
          })
          .catch(err => {
            reject(err);
          });
      } else {
        reject(new Error("..."));
      }
    });
  }
};

export default {
  // namespaced: true, // 使其成为带命名空间的模块
  state,
  mutations,
  actions
};



