import { LimitType } from "@/utils/enum";

const courseHistoryRouter = [
  {
    path: "/history",
    name: "history",
    meta: {
      title: "课程",
      from: "/home-course",
      cache: true,
      limits: [LimitType.afterCourse]
    },
    component: () => import("@/pages/history/history.vue")
  },
  {
    path: "/history/screenQuestion/:type",
    name: "historyScreen",
    meta: {
      title: "截屏提问",
      from: "/history",
      limits: [LimitType.afterCourse]
    },
    component: () => import("@/pages/history/screenQuestion/index.vue")
  },
  {
    path: "/history/screenQuestionInfo/:id",
    name: "historyScreenInfo",
    meta: { title: "截屏提问内容", from: "", limits: [LimitType.afterCourse] },
    component: () => import("@/pages/history/screenQuestion/Detail.vue")
  },
  {
    path: "/history/classPractise/:id/:bankId/:total",
    name: "historyPractise",
    meta: {
      title: "随堂练习",
      from: "/history",
      limits: [LimitType.afterCourse]
    },
    component: () => import("@/pages/history/classPractise/index.vue")
  },
  {
    path: "/history/groupDiscussion/:id/:groupId/:name",
    name: "historyDiscussion",
    meta: {
      title: "小组讨论",
      from: "/history",
      limits: [LimitType.afterCourse]
    },
    component: () => import("@/pages/history/groupDiscussion/index.vue")
  },
  {
    path: "/history/teamRank/:id/:groupId/:name",
    name: "historyTeamRank",
    meta: { title: "小组讨论", from: "", limits: [LimitType.afterCourse] },
    component: () => import("@/pages/history/groupDiscussion/teamRank.vue")
  },
  {
    path: "/history/coursewareView/:id/:fileExt/:name",
    name: "historyCoursewareView",
    meta: { title: "课件预览", from: "", limits: [LimitType.afterCourse] },
    component: () => import("@/pages/history/courseware/fileView.vue")
  },
  {
    path: "/history/boardnote",
    name: "historyBoardnote",
    meta: { title: "板书", from: "/history", limits: [LimitType.afterCourse] },
    component: () => import("@/pages/history/boardNote/index.vue")
  },
  {
    path: "/history/groupNote",
    name: "historyGroupNote",
    meta: {
      title: "小组笔记",
      from: "/history",
      limits: [LimitType.afterCourse]
    },
    component: () => import("@/pages/history/groupNote/index.vue")
  },
  {
    path: "/history/note",
    name: "historyNote",
    meta: { title: "笔记", from: "/history", limits: [LimitType.afterCourse] },
    component: () => import("@/pages/history/note/index.vue")
  },
  {
    path: "/history/note/detail",
    name: "historyNoteDetail",
    meta: { title: "笔记详情", from: "", limits: [LimitType.afterCourse] },
    component: () => import("@/pages/history/note/Detail.vue")
  },
  {
    path: "/history/liveLesson",
    name: "historyLiveLesson",
    meta: {
      title: "课堂实况",
      from: "/history",
      limits: [LimitType.afterCourse]
    },
    component: () => import("@/pages/history/liveLesson/index.vue")
  },
  {
    path: "/history/livePlayback",
    name: "historyLivePlayback",
    meta: {
      title: "课堂实况",
      from: "/history/liveLesson",
      limits: [LimitType.afterCourse]
    },
    component: () => import("@/pages/course/liveLesson/playback.vue")
  },
  {
    path: "/history/question",
    name: "historyQuestion",
    meta: {
      title: "我的提问",
      from: "/history",
      limits: [LimitType.afterCourse]
    },
    component: () => import("@/pages/history/question/index.vue")
  }
];

export default courseHistoryRouter;



