import {
  storages,
  setLocalStorage,
  getLocalStorage,
  setSession,
  getSession,
  removeSession
} from "@/utils/auth";
import mainHttp from "@/api/index";
import { GroupType } from "@/utils/enum";
import { FuncType, sendToAPP } from "@/utils/interFlow";

const sendFreshGroup = (state, val) => {
  const obj = {
    isJoin: val,
    groupIp: state.groupIP || "",
    groupName: state.group["groupName"] || "",
    maxUser: state.group["groupMax"] || "0",
    connectUser: state.group["currentNum"] || "0"
  };
  console.log("推送新值：", obj);
  sendToAPP(FuncType.groupInfo, JSON.stringify(obj));
};

const state = {
  course: getSession(storages.course, true),
  courseID: getSession(storages.courseID),
  nowCourse: getLocalStorage(storages.nowCourse),

  gatewayIP: getLocalStorage(storages.gatewayIP),
  recIP: getLocalStorage(storages.recIP),

  class: getLocalStorage(storages.class, true),
  classID: getLocalStorage(storages.classID),
  classStudentID: getLocalStorage(storages.classStudentID),

  group: getSession(storages.group, true),
  groupID: getSession(storages.groupID),
  groupIP: getSession(storages.groupIP),
  grouping: {},
  groupType: getSession(storages.groupType),

  screenQuestion: {},
  classPractice: {},
  groupDiscussion: {},
  boardNote: {},
  courseWareState: {},
  noteRefresh: false,
  // 临时课堂实录信息
  liveLesson: null
};

const mutations = {
  SET_COURSE: (state, value) => {
    state.course = value;
    setSession(storages.course, value);
  },
  SET_COURSE_ID: (state, value) => {
    state.courseID = value;
    setSession(storages.courseID, value);
  },
  SET_NOW_COURSE: (state, value) => {
    state.nowCourse = value;
    setLocalStorage(storages.nowCourse, value);
  },

  SET_GATEWAY_IP: (state, value) => {
    state.gatewayIP = value;
    setLocalStorage(storages.gatewayIP, value);
  },
  SET_REC_IP: (state, value) => {
    state.recIP = value;
    setLocalStorage(storages.recIP, value);
  },

  SET_CLASS: (state, value) => {
    state.class = value;
    setLocalStorage(storages.class, value);
  },
  SET_CLASS_ID: (state, value) => {
    state.classID = value;
    setLocalStorage(storages.classID, value);
  },
  SET_CLASS_STUDENT_ID: (state, value) => {
    state.classStudentID = value;
    setLocalStorage(storages.classStudentID, value);
  },

  SET_GROUP: (state, value) => {
    state.group = value;
    setSession(storages.group, value, 1000 * 60 * 60 * 2);
  },
  SET_GROUP_IP: (state, value) => {
    state.groupIP = value;
    setSession(storages.groupIP, value, 1000 * 60 * 60 * 2);
  },
  SET_GROUP_ID: (state, value) => {
    const oldGroupId = state.groupID;
    state.groupID = value;
    setSession(storages.groupID, value, 1000 * 60 * 60 * 2);
    if (oldGroupId !== value && oldGroupId) {
      window.GatewayHub.invoke("QuitGroup", oldGroupId)
        .then(res => {
          console.log("退出讨论组：", value);
        })
        .catch(err => {
          console.log("err：", err);
        });
    }

    setTimeout(() => {
      window.GatewayHub.invoke("JoinGroup", value).then(res => {
        console.log("加入讨论组：", value);
        sendFreshGroup(state, true);
      }).catch(err => {
        console.log("err：", err);
      });
    }, 500);
  },
  QUIT_GROUP: state => {
    let groupID = state.groupID;
    if (groupID) {
      window.GatewayHub.invoke("QuitGroup", groupID).then(res => {
        console.log("退出讨论组：", groupID);
      }).catch(err => {
        console.log("err：", err);
      });
    }
    state.group = "";
    state.groupIP = null;
    state.groupID = null;
    setSession(storages.groupID, "", 1000 * 60 * 60 * 2);
    setSession(storages.groupIP, null, 1000 * 60 * 60 * 2);
    setSession(storages.group, null, 1000 * 60 * 60 * 2);
    sendFreshGroup(state, false); // 更新分组信息给APP
  },
  SET_GROUPING: (state, value) => {
    state.grouping = value;
  },
  SET_GROUP_TYPE: (state, value) => {
    state.groupType = value;
    setSession(storages.groupType, value);
  },

  SET_SCREEN_QUESTION: (state, value) => {
    state.screenQuestion = value;
  },
  SET_CLASS_PRACTICE: (state, value) => {
    state.classPractice = value;
  },
  SET_GROUP_DISCUSSION: (state, value) => {
    state.groupDiscussion = value;
  },
  SET_BOARD_NOTE: (state, value) => {
    state.boardNote = value;
  },
  SET_COURSE_WARE_STATE: (state, value) => {
    state.courseWareState = value;
  },
  SET_NOTE_REFRESH: (state, value) => {
    state.noteRefresh = value;
  },
  SET_LIVE_LESSON: (state, value) => {
    state.liveLesson = value;
  },
  /**
   * 退出课程了
   * @param {*} state
   * @param {*} value
   */
  QUIT_COURSE (state) {
    state.course = null;
    removeSession(storages.course);
    state.courseID = "";
    removeSession(storages.courseID);
    state.group = null;
    removeSession(storages.group);
    state.groupID = null;
    removeSession(storages.groupID);
    state.groupType = null;
    removeSession(storages.groupType);

    state.screenQuestion = {};
    state.classPractice = {};
    state.groupDiscussion = {};
  }
};

const actions = {
  /**
   * 加入班级请求
   * @param {*} reqData
   * @returns {Promise<any>}
   */
  JoinClassByUser ({ commit }, reqData) {
    return new Promise((resolve, reject) => {
      mainHttp.class.joinClass(reqData).then(res => {
        if (res.Result) {
          commit("SET_CLASS_ID", res["ClassID"]);
          commit("SET_CLASS_STUDENT_ID", res["CSID"]);
          commit("SET_COURSE_ID", res["CIID"]);
        }
        resolve(res);
      }).catch(error => {
        reject(error);
      });
    });
  },

  /**
   * 课程校验
   * @param {*} userInfo
   * @returns {Promise<any>}
   */
  ValidateCourse ({ commit, getters }, reqData) {
    return new Promise((resolve, reject) => {
      mainHttp.classInfo.getStudentCourse({
        studentID: getters.studentID
      }).then(res => {
        const nowCourseID = getters.nowCourse ? getters.nowCourse["CIID"] : "";
        if (res.Result && res.Datas && res.Datas.length > 0 && res.Datas[0]["CIID"] === nowCourseID) {
          resolve(true);
        } else {
          commit("SET_NOW_COURSE", null);
          resolve(false);
        }
      }).catch(error => {
        commit("SET_NOW_COURSE", null);
        reject(error);
      });
    });
  },

  /**
   * 获取课程服务请求
   * @param {*} classRoomId
   * @returns {Promise<any>}
   */
  GetCourseServiceByUser ({ commit }, classRoomId) {
    return new Promise((resolve, reject) => {
      mainHttp.interactConfig.getData({
        clsRoomID: classRoomId
      }).then(res => {
        if (res.Result) {
          commit("SET_GATEWAY_IP", res.Data["InIP"]);
          commit("SET_REC_IP", JSON.parse(res.Data["ConfigInfo"]).RecIP);
        }
        resolve(res);
      }).catch(error => {
        reject(error);
      });
    });
  },
  /**
   * 获取当前分组
   * @returns {Promise<any>}
   */
  GetGroupByUser ({ commit, getters }) {
    return new Promise((resolve, reject) => {
      mainHttp.group.getStudentGroup({
        ciID: getters.courseID,
        studentID: getters.studentID
      }).then(res => {
        if (res.Result && res.Data) {
          let groupItem = {
            gatewayID: res.Data["GatewayID"],
            groupID: res.Data["GroupID"],
            groupName: res.Data["GroupName"],
            groupMax: res.Data["GroupNumber"],
            groupingID: res.Data["GroupingID"],
            studentID: res.Data["StudentID"],
            memberList: res["memberList"],
            currentNum: res["memberList"] ? res["memberList"].length : 0
          };

          let groupIP = res["GatewayIP"] || "";
          commit("SET_GROUP_IP", groupIP);
          commit("SET_GROUP", groupItem);
          // if (!user.state.groupID || user.state.groupID !== res.Data["GroupID"]) {
          commit("SET_GROUP_ID", res.Data["GroupID"]);
          // }
          if (res.GroupingType) commit("SET_GROUP_TYPE", res.GroupingType);
        } else {
          commit("QUIT_GROUP");
        }
        resolve(res);
      }).catch(error => {
        reject(error);
      });
    });
  },

  /**
   * 加入分组
   * @param {*} groupID
   * @param {*} group
   */
  JoinGroup ({ commit, getters }, groupInfo) {
    return new Promise((resolve, reject) => {
      mainHttp.group.postJoinGroup({
        ciID: getters.courseID,
        studentID: getters.studentID,
        groupID: groupInfo.groupID
      }).then(res => {
        if (res.Result) {
          commit("SET_GROUP_ID", groupInfo.groupID);
          commit("SET_GROUP", groupInfo.group);
          commit("SET_GROUP_IP", groupInfo.group["gatewayIP"] || "");
        }
        resolve(res);
      }).catch(error => {
        reject(error);
      });
    });
  },

  /**
   * 退出当前分组
   */
  QuitGroup ({ commit, getters }) {
    return new Promise((resolve, reject) => {
      mainHttp.group.postQuitGroup({
        ciID: getters.courseID,
        groupID: getters.groupID,
        studentID: getters.studentID
      }).then(res => {
        if (res.Result) {
          commit("QUIT_GROUP");
          commit("SET_GROUP_TYPE", GroupType.Free);
        }
        resolve(res);
      }).catch(error => {
        console.log(error);
        reject(error);
      });
    });
  }
};

export default {
  // namespaced: true,
  state,
  mutations,
  actions
};



