import Vue from "vue";
import mainHttp from "@/api/index";
// 参考：https://www.bbsmax.com/A/D854PpjW5E/
// 全局注册自定义指令，用于处理图片链接，减少冗余代码
Vue.directive('real-img', async function(el, binding) { // 指令名称为：real-img
  const imgParams = binding.value;// 获取图片地址
  if (imgParams && imgParams.length >= 2) {
    const url = mainHttp.common.downloadPhoto(imgParams[0], imgParams[1]);
    el.setAttribute('src', url);
    el.classList.add('real-img');
    el.setAttribute('onerror', 'this.src="' + require('@/assets/boy_img.png') + '"');
  }
});

