<template>
  <van-image-preview v-model="previewShow" :showIndex="false" :images="previewImages" @close="close">
  </van-image-preview>
</template>

<script>
import { ImagePreview } from "vant";

export default {
  name: "r-image-preview",
  components: { [ImagePreview.Component.name]: ImagePreview.Component },
  data () {
    return {
      previewShow: false,
      previewImages: []
    };
  },
  methods: {
    preview () {
      if (this.$store.getters.imagePreview) {
        this.previewImages = [this.$store.getters.imagePreview];
        this.previewShow = true;
      }
    },
    close () {
      this.previewImages = [];
      this.$store.commit("SET_IMAGE_PREVIEW", "");
    }
  },
  watch: {
    "$store.getters.imagePreview": {
      immediate: true,
      handler: "preview"
    }
  }
}
</script>

<style lang="less" scoped>

</style>



