import { httpGet, httpPost, basePath } from "./../request";

const Sign = `${basePath}/I_SignService/`;
export default {
    getList: data => {
        return httpGet(
            `${Sign}GetDatasToStu`,
            data
        );
    },
    dataList: data => {
        return httpGet(
            `${Sign}GetInClassData`,
            data
        );
    },
    postSign: data => {
        return httpPost(
            `${Sign}SignIn`,
            data
        );
    }
};


