import { httpGet, basePath } from "./../request";

const ClassHour = `${basePath}/I_ClassHourService/`;
export default {
    dataList: data => {
        return httpGet(
            `${ClassHour}GetDataByCourseID`,
            data
        );
    },
    getPracticeTaskList: data => {
        return httpGet(
            `${ClassHour}GetStudentCPTaskDatas`,
            data
        );
    },
    getCourseWareTaskList: data => {
        return httpGet(
            `${ClassHour}GetStudentCWTaskDatas`,
            data
        );
    }
};


