import { httpGet, httpPost, basePath } from "./../request";

const CoursewareComment = `${basePath}/I_CoursewareEvalService/`;
export default {
    getCommentData: data => {
        return httpGet(
            `${CoursewareComment}GetStudentData`,
            data
        );
    },
    // 提交点评
    postCommentSave: data => {
        return httpPost(
            `${CoursewareComment}Save`,
            data
        );
    }
};


