import { httpGet, basePath } from "./../request";

const GroupNote = `${basePath}/I_GroupNoteService/`;
export default {
  getList: data => {
    return httpGet(
      `${GroupNote}GetDatasByStudentID`,
      data
    );
  },
  getListByGroup: data => {
    return httpGet(
      `${GroupNote}GetDatasByGroupID`,
      data
    );
  }
};



