import router from "./router";
import store from "./store";
import { getLocalStorage, storages } from "@/utils/auth";
import { LimitType } from "@/utils/enum";

const whiteList = ["/login", "/agreement", "/policy"]; // 路由白名单
const toLoginList = ["/login", "/", "/my-zone"]; // 可以跳转登录页的路由

router.beforeEach(async (to, from, next) => {
  // console.log("to:", to);
  // console.log("from:", from);
  store.commit("SET_LOADING", true);
  //   loading.setAttribute("style", "display: block;");
  //是否已登录
  if (getLocalStorage(storages.userId)) {
    if (!store.getters.courseID) {
      //当前选择课程由vuex控制，未选中课程，子级页面禁止访问
      if (to.meta.limits && to.meta.limits.indexOf(LimitType.inCourse) > -1) next("/home-course");
      if (to.meta.limits && to.meta.limits.indexOf(LimitType.afterCourse) > -1) next("/home-course");
    } else if (!store.getters.groupID) {
      //当前选择课程由vuex控制，尚无分组数据，组页面禁止访问
      if (to.meta.limits && to.meta.limits.indexOf(LimitType.group) > -1) next("/course");
    }

    if (to.path === "/login" && toLoginList.indexOf(from.path) === -1) {
      // 已经登录的，限制跳转登录页
      next(false);
    } else {
      next();
    }
  } else {
    /* has no token*/
    if (whiteList.indexOf(to.path) > -1) {
      // 路由白名单，直接跳转
      next();
    } else {
      next("/login");
    }
  }

  // 跳转是退出课程？
  const exitCourse = !to.meta.limits || (to.meta.limits.indexOf(LimitType.inCourse) === -1 && to.meta.limits.indexOf(LimitType.afterCourse) === -1);
  // 离开课程页处理
  if (exitCourse) {
    if (from.meta.limits && from.meta.limits.indexOf(LimitType.inCourse) > -1) {
      // 离开指定课程，清掉缓存和关闭signalR服务
      store.commit("QUIT_COURSE");
    } else if (from.meta.limits && from.meta.limits.indexOf(LimitType.afterCourse) > -1) {
      // 离开指定课程，清掉缓存
      store.commit("QUIT_COURSE");
    } else if (from.path === "/" && to.path === "/login") {
      // 启动应用的情况下，清掉缓存和关闭signalR服务
      store.commit("QUIT_COURSE");
    }
  }
});

router.afterEach((to, from) => {
  // 延时关闭
  // setTimeout(() => {
  store.commit("SET_LOADING", false);
  // }, 100);
  //   loading.setAttribute("style", "display: none;");
  let toIndex = store.getters.navigations.findIndex(r => r.path === to.path);
  if (toIndex >= 0) { // 存在该路径
    let len = store.getters.navigations.length - 1;
    if (toIndex === len) { // 当前路径是最后一条，证明是同一个页面
      console.log("refresh")
    } else { // 后退
      const navigations = store.getters.navigations.slice(0, toIndex + 1);
      store.commit("SET_ROUTER_DIRECTION", 'back'); // 后退标志
      store.commit("SET_NAVIGATIONS", navigations) // 删除当前路径后面的路径

      // this.$store.commit("SET_CACHE_COMPONENTS", to.name);
    }
  } else { // 不存在该路径
    const navigations = store.getters.navigations.concat([{
      path: to.path,
      params: to.params,
      query: to.query,
      name: to.name
    }]);

    if (to.meta && to.meta.cache && to.name) {
      // console.log("cacheComponents:", store.getters.cacheComponents);
      // const cacheList = [...new Set(store.getters.cacheComponents.concat(to.name))];
      // console.log("cacheList:", cacheList);
      // store.commit('SET_CACHE_COMPONENTS', cacheList)
    }
    store.commit("SET_ROUTER_DIRECTION", 'forward'); // 前进标志
    store.commit('SET_NAVIGATIONS', navigations)// 保存该连接
  }
});



