import { getNowDateTime, NowTimeStamp, strConvertJson } from "@/utils/index";
import { GroupType, GroupFunc, DiscussFunc, ScreenFunc, PracticeFunc, LimitType } from "@/utils/enum";
import { FuncType, sendToAPP } from "@/utils/interFlow";

export default {
  data () {
    return {
      conn: null,
      reConnect: false,
      heartTimeStamp: 0
    };
  },
  mounted () {
    this.$nextTick(() => {
      this.startGatewaySignalR();

      window.addEventListener('visibilitychange', this.courseWakeUp);
      this.$on("hook:destroyed", () => {
        window.removeEventListener("visibilitychange", this.courseWakeUp);
      });
    });
  },
  methods: {
    /**
     * 页面苏醒处理
     */
    courseWakeUp () {
      if (!document.hidden) {
        console.log(getNowDateTime(), "页面激活");
        this.reConnect = false;
        this.startGatewaySignalR();
        // 更新分组
        if (this.$route.meta.limits.indexOf(LimitType.group) > -1) {
          this.$store.commit("SET_GROUP_ID", this.$store.getters.groupID);
        }
      }
    },
    /**
     * 开启signalR连接
     */
    openCourseSocket () {
      if (!this.$store.getters.interactServer || !this.$store.getters.interactPort) return;
      this.reConnect = true;
      this.conn = $.hubConnection("http://" + this.$store.getters.interactServer + ":" + this.$store.getters.interactPort);
      window.GatewayHub = this.conn.createHubProxy("InteractHub");
      this.conn.qs = { StudentID: this.$store.getters.studentID };
      this.signalRFunction();

      this.conn.start().done(() => {
        // console.log(getNowDateTime(), "lesson-SignalR服务启动成功");
        this.signalRGroup();
      }).fail(err => {
        console.log(getNowDateTime(), "lesson-SignalR服务连接失败", err);
      });

      this.conn.disconnected(() => {
        console.log(getNowDateTime(), "lesson-SignalR连接已断开");
        if (this.$store.getters.courseID) {
          this.$toast("请重新连接课程");
          this.turnPath("/home-course");
        }
      });

      // stateChanged：连接状态更改时引发。 提供的旧状态和新的状态 （连接、 已连接、 正在重新连接或已断开连接）。
      this.conn.stateChanged(data => {
        console.log("通信状态：", data.newState, this.reConnect);
        if (data.newState !== 1) {
          this.startGatewaySignalR();
        } else {
          this.signalRConnected();
        }
      });
    },
    /**
     * signalR组管理
     */
    signalRGroup () {
      this.signalRGroupFunction(this.$store.getters.courseID, "课堂");
      this.signalRGroupFunction(this.$store.getters.studentID, "学生");

      // 自动加入分组
      this.$store.dispatch("GetGroupByUser").then(res => {
        if (res.Result) {
        }
      });
    },
    signalRGroupFunction (groupValue, msg) {
      window.GatewayHub.invoke("QuitGroup", groupValue).then(() => {
        window.GatewayHub.invoke("JoinGroup", groupValue).catch(err => {
          console.log("JoinGroup-" + msg + groupValue + "失败：", err);
        });
      }).catch(err => {
        console.log("QuitGroup-" + msg + groupValue + "失败：", err);
        window.GatewayHub.invoke("JoinGroup", groupValue).catch(errMsg => {
          console.log("退出失败后，JoinGroup-" + msg + groupValue + "失败：", errMsg);
        });
      });
    },
    /**
     * signalR响应事件
     */
    signalRFunction () {
      // 课程
      window.GatewayHub.on("ReceiveStartClass", courseId => {
        // console.log("ReciveStartClass:", courseId);
        this.ReceiveStartClass({ state: 1 });
      });
      window.GatewayHub.on("ReceiveOverClass", value => {
        this.ReceiveStartClass({ state: 0 });
      });

      // 接收消息(截屏提问) --接收题目
      window.GatewayHub.on("ReceiveSSSubject", subjectId => {
        this.receiveScreenSubject({ id: subjectId, type: ScreenFunc.init });
      });
      // --接收答案
      window.GatewayHub.on("ReceiveSSAnswer", subjectId => {
        this.receiveScreenSubject({ id: subjectId, type: ScreenFunc.acceptAnswer });
      });
      // --提交调研
      window.GatewayHub.on("ReceiveSSSurvey", subjectId => {
        this.receiveScreenSubject({ id: subjectId, type: ScreenFunc.survey });
      });
      // 截屏提问--开始抢答
      window.GatewayHub.on("ReceiveSSStartResponder", subjectId => {
        this.receiveScreenSubject({ id: subjectId, type: ScreenFunc.responder });
      });

      // 接收消息(随堂练习)
      window.GatewayHub.on("ReceivePSStart", data => {
        console.log("ReceivePSStart:", data);
        const value = strConvertJson(data);
        this.receiveClassPractise({
          id: value["CIPID"],
          bankId: value["PLID"],
          no: value["PSSerial"],
          total: value["SubjectTotal"],
          type: PracticeFunc.start
        });
      });
      window.GatewayHub.on("ReceivePSEnd", data => {
        console.log("ReceivePSEnd:", data);
        this.receiveClassPractise({
          id: "",
          type: PracticeFunc.end
        });
      });
      window.GatewayHub.on("ReceivePSubject", data => {
        console.log("ReceivePSubject:", data);
        const value = strConvertJson(data);
        this.receiveClassPractise({
          id: value["CIPID"],
          bankId: value["PLID"],
          no: value["PSSerial"],
          // total: data["SubjectTotal"],
          type: PracticeFunc.init
        });
      });
      window.GatewayHub.on("ReceivePSAnswer", practiceId => {
        console.log("ReceivePSAnswer:", practiceId);
        this.receiveClassPractise({ id: practiceId, type: PracticeFunc.acceptAnswer });
      });
      window.GatewayHub.on("ReceivePSSurvey", practiceId => {
        console.log("ReceivePSSurvey:", practiceId);
        this.receiveClassPractise({ id: practiceId, type: PracticeFunc.survey });
      });

      // 小组分组
      window.GatewayHub.on("ReceiveGrouping", groupType => {
        this.receiveGrouping({
          groupType: groupType,
          type: GroupFunc.init,
          time: NowTimeStamp()
        }); //开始分组
      });
      window.GatewayHub.on("ReceiveUpdateGroupName", groupName => {
        console.log(groupName);
        this.receiveGrouping({ type: GroupFunc.reName, time: NowTimeStamp() }); //更新组名
      });
      window.GatewayHub.on("ReceiveJoinGroup", groupID => {
        this.receiveGrouping({ type: GroupFunc.newJoin, time: NowTimeStamp() }); // 接收加入小组
      });
      window.GatewayHub.on("ReceiveQuitGroup", groupID => {
        this.receiveGrouping({ type: GroupFunc.quit, time: NowTimeStamp() }); // 接收退出小组
      });
      window.GatewayHub.on("ReceiveSetGroupLeader", studentId => {
        this.receiveGrouping({ type: GroupFunc.setLead, time: NowTimeStamp() }); // 更新组长
      });

      //小组讨论
      window.GatewayHub.on("ReceiveGroupSubject", discussionId => {
        this.receiveGroupDiscussion({ type: DiscussFunc.init, value: discussionId });
      });
      window.GatewayHub.on("ReceiveGroupDiscuss", data => {
        const discussion = strConvertJson(data);
        this.receiveGroupDiscussion({ type: DiscussFunc.newMsg, value: discussion });
      });
      window.GatewayHub.on("ReceiveGroupGood", value => {
        this.receiveGroupDiscussion({ type: DiscussFunc.good, value: value });
      });
      window.GatewayHub.on("ReceiveGroupScore", value => {
        this.receiveGroupDiscussion({ type: DiscussFunc.score, value: value });
      });

      // 板书分享
      window.GatewayHub.on("ReceiveScreenShare", value => {
        this.receiveBoardNote({ id: value });
      });

      // 广播
      window.GatewayHub.on("ReceiveStartBroadcast", data => {
        const value = strConvertJson(data);
        console.log("start:", value);
        // receiveBoardCast({ ip: value.BType, ids: value.ID || "", state: 1 });
        this.receiveBoardCast({ ip: value.IP || value.BType, ids: value.ID || "", state: 1 });
      });
      window.GatewayHub.on("ReceiveStopBroadcast", value => {
        console.log("stop:", value);
        this.receiveBoardCast({ ip: "000", ids: "", state: 0 });
      });

      // 投屏
      window.GatewayHub.on("ReceiveStartThrowScreen", value => {
        console.log("startThrow:", value);
        this.receiveThrowScreen({ ip: value, state: 1 });
      });
      window.GatewayHub.on("ReceiveStopThrowScreen", value => {
        console.log("stopThrow:", value);
        this.receiveThrowScreen({ ip: value, state: 0 });
      });

      // 被投屏
      window.GatewayHub.on("ReceiveShowThrowScreen", value => {
        console.log("startBeThrow:", value);
        this.receiveBeThrowScreen({ ip: value, state: 1 });
      });
      window.GatewayHub.on("ReceiveStopShowThrowScreen", value => {
        console.log("stopBeThrow:", value);
        this.receiveBeThrowScreen({ ip: value, state: 0 });
      });

      // 投屏状态
      window.GatewayHub.on("ReceiveIsAllowThrowScreen", value => {
        this.receiveThrowScreenState({ state: value });
      });

      // 课件分享
      window.GatewayHub.on("ReceiveSyncCourseware", value => {
        this.receiveShareCourseWare({ state: 1 });
      });
      window.GatewayHub.on("ReceiveCancelCourseware", value => {
        this.receiveShareCourseWare({ state: 0 });
      });

      // 撤销指令
      window.GatewayHub.on("ReceiveRevoke", () => {
        this.turnPath("/course");
      });
    },
    /**
     * 定时启动signalR服务，成功清除定时器
     */
    startGatewaySignalR () {
      if (!this.reConnect) {
        // console.log(getNowDateTime(), "请求启动lesson-SignalR");
        this.reConnectServer();
      }
    },
    /**
     * 递归启动signalR服务
     */
    reConnectServer () {
      try {
        // 防止返回课程再次触发，先不改路由路径
        if (this.$route.matched.findIndex(r => r.path === "/courseIndex") === -1) return;
        if (!this.conn || this.conn.state !== 1) {
          console.log(getNowDateTime(), "检测到lesson-SignalR服务未连接");
          this.openCourseSocket();
          setTimeout(() => {
            this.reConnectServer();
          }, 3000);
        } else {
          console.log("通信服务连接成功，关闭重连事件", this.conn.state, this.conn);
          this.reConnect = false;
        }
      } catch (e) {
        setTimeout(() => {
          this.reConnectServer();
        }, 3000);
      }
    },
    /**
     * signalR服务启动成功后执行的程序
     */
    signalRConnected () {
      this.getScreenInfo();
      this.validateCourse();
      this.getLocalIP();

      // 需先初始化心跳记录时间点
      this.heartTimeStamp = 0;
      this.startHeart();
    },
    /**
     * 停止signalR服务
     */
    stopServer () {
      // this.startServer = false;
      // this.reConnect = false;
      console.log(getNowDateTime(), "关闭服务");
      if (this.conn) {
        this.conn.stop(false, true);
      }
    },
    /**
     * 更新本地IP
     */
    getLocalIP () {
      sendToAPP(FuncType.initLocalIP, "").then(res => {
        if (res) this.$store.commit("SET_IP", res);
      });
    },

    startRegister () {
      let IP = this.$store.getters.localIP;
      sendToAPP(FuncType.initLocalIP, "").then(res => {
        if (res) this.$store.commit("SET_IP", res);
      });
      let deviceType = -1;
      if (window.Android) {
        deviceType = 1;
      } else if (window.webkit) {
        deviceType = 2;
      }
      let reqData = {
        ciID: this.$store.getters.courseID,
        studentID: this.$store.getters.studentID,
        studentIP: this.$store.getters.localIP || IP,
        deviceType: deviceType
      };
      this.$mainHttp.class.postEnterClass(reqData).then(res => {
        if (res.Result) {
          console.log(getNowDateTime(), "更新IP请求成功");
        }
      }).catch(() => {
      });
    },
    startHeart () {
      let timeStamp = new Date().getTime();
      let timeOut = this.$config.SignalRHeartTime || 2000;
      //防止多次调用，频繁执行
      if (timeStamp - this.heartTimeStamp > timeOut) {
        this.heartTimeStamp = timeStamp;
        if (this.conn && this.conn.state === 1) {
          let deviceType = -1;
          if (window.Android) {
            deviceType = 1;
          } else if (window.webkit) {
            deviceType = 2;
          }
          let message = {
            Type: 1,
            ID: this.$store.getters.studentID || "",
            IP: this.$store.getters.localIP || "",
            deviceType: deviceType,
            CIID: this.$store.getters.courseID || "",
            TeacherID: this.$store.getters.course["TeacherID"] || ""
          };

          window.GatewayHub.invoke(
            "Heart",
            JSON.stringify(message)
          ).then(res => {
            console.log("Heart-res:", res);
            setTimeout(() => {
              this.startHeart();
            }, timeOut);
          }).catch(err => {
            console.log("Heart-err：", err);
          });
        } else {
          setTimeout(() => {
            this.startHeart();
          }, timeOut);
        }
      }
    },
    validateCourse () {
      this.$store.dispatch('ValidateCourse').then(res => {
        if (!res) {
          this.exitCourse();
        }
      }).catch(err => {
      });
    },
    exitCourse () {
      const toast = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: '该教室已有新课程，即将在 3 秒后退出'
      });

      let second = 3;
      const timer = setInterval(() => {
        second--;
        if (second) {
          toast.message = `该教室已有新课程，即将在  ${second} 秒后退出`;
        } else {
          clearInterval(timer);
          // 手动清除 Toast
          this.$toast.clear();

          this.turnPath("/home-course");
        }
      }, 1000);
    },
    getScreenInfo () {
      this.$mainHttp.gateway.getScreenConfig({
        clsRoomID: this.$store.getters.course["ClassroomID"]
      }).then(res => {
        if (res.Result) {
          delete res.Result;
          const info = JSON.parse(JSON.stringify(res));
          console.log(info);
          sendToAPP(FuncType.screenSetting, JSON.stringify(info)).then(() => {
          }).catch(() => {
          });
        }
      }).catch(() => {
      });
    },
    /**
     * 课程
     */
    ReceiveStartClass (value) {
      if (value.state === 0) {
        this.$store.commit("QUIT_COURSE");
        this.turnPath("/home-course");
      }
    },

    /**
     * 截屏提问
     * @param {*} value
     */
    receiveScreenSubject (value) {
      console.log("receiveScreenSubject:", value);
      this.$store.commit("SET_SCREEN_QUESTION", { ...value, time: NowTimeStamp() });
      this.turnPath("/screenQuestion/" + value.id);
    },

    /**
     * 随堂练习
     * @param {*} value
     */
    receiveClassPractise (value) {
      this.$store.commit("SET_CLASS_PRACTICE", { ...value, time: NowTimeStamp() });
      if (value.type === PracticeFunc.end) {
        this.turnPath("/quiz/1");
      } else {
        this.turnPath("/classPractice/" + value.id + "/" + value.bankId);
      }
    },

    /**
     * 接收分组
     */
    receiveGrouping (func) {
      this.$store.commit("SET_GROUPING", func);
      if (func.type === GroupFunc.init && func.groupType + "" === GroupType.Free) {
        console.log("接收分组:", func.type);
        this.initGroupDeal();
        return;
      }
      this.$store.dispatch("GetGroupByUser").then(res => {
        if (res.Result) {
          if (func.type === GroupFunc.quit && !this.$store.getters.groupID) {
            // 重新分组
            console.log("接收分组:", func.type);
            this.initGroupDeal();
          } else {
            this.sendFreshGroup(true);
            this.turnPath("/groupInfo");
          }
        }
      });
    },

    /**
     * 重新分组方法
     */
    initGroupDeal () {
      this.$store.commit("QUIT_GROUP");
      this.$store.commit("SET_GROUP_TYPE", GroupType.Free);

      this.turnPath("/joinGroup");
      this.sendFreshGroup(false);
    },

    /**
     * 小组讨论
     * @param {*} value
     */
    receiveGroupDiscussion (value) {
      this.$store.commit("SET_GROUP_DISCUSSION", { ...value, time: NowTimeStamp() });
      // 讨论更新的不用自动切
      if (value.type !== DiscussFunc.newMsg) {
        this.turnPath("/groupDiscussion");
      }
    },

    /**
     * 板书分享
     * @param {*} value
     */
    receiveBoardNote (value) {
      this.$store.commit("SET_BOARD_NOTE", { ...value, time: NowTimeStamp() });

      this.turnPath("/blackboardWriting");
    },

    /**
     * 推送新的分组信息
     */
    sendFreshGroup (isJoin) {
      console.log("推送新的分组信息");
      let groupIp = this.$store.getters.groupIP || "";
      let groupName = "";
      let maxUser = "0";
      let connectUser = "0";
      if (this.$store.getters.group) {
        groupName = this.$store.getters.group["groupName"] || "";
        maxUser = parseInt(this.$store.getters.group["groupMax"] || "0");
        connectUser = parseInt(this.$store.getters.group["currentNum"] || "0");
      }

      let obj = {
        isJoin: isJoin,
        groupIp: groupIp,
        groupName: groupName,
        maxUser: maxUser || 0,
        connectUser: connectUser || 0
      };
      console.log("推送新值：", obj);
      sendToAPP(FuncType.groupInfo, JSON.stringify(obj));
    },

    /**
     * 跳转链接
     * @param toPath 即将跳转的地址
     */
    turnPath (toPath) {
      sendToAPP(FuncType.toPath, toPath);
      if (this.$route.path.indexOf(toPath) === -1) {
        this.$router.replace(toPath);
      }
    },

    /**
     * 推送广播信息
     * @param {*} value
     */
    receiveBoardCast (value) {
      if (value.ids && value.ids.indexOf(this.$store.getters.studentID) > -1) {
      } else {
        let funcType = FuncType.broadcastStop;
        if (value.state === 1) {
          funcType = (value.ip + "") !== "1" && (value.ip + "") !== "0" ? FuncType.broadcastStartUrl : FuncType.broadcastStart;
        }
        sendToAPP(funcType, value.ip);
      }
    },

    /**
     * 推送投屏信息
     * @param {*} value
     */
    receiveThrowScreen (value) {
      sendToAPP(value.state === 1 ? FuncType.throwScreenStart : FuncType.throwScreenStop, value.ip || "-1");
    },

    /**
     * 推送被投屏信息
     * @param {*} value
     */
    receiveBeThrowScreen (value) {
      sendToAPP(value.state === 1 ? FuncType.beThrowScreenStart : FuncType.beThrowScreenStop, value.ip || "-1");
    },

    /**
     * 推送投屏状态
     * @param {*} value
     */
    receiveThrowScreenState (value) {
      sendToAPP(FuncType.searchScreenState, value.state ? "1" : "0");
    },

    /**
     * 课件分享状态
     * @param value
     */
    receiveShareCourseWare (value) {
      this.$store.commit("SET_COURSE_WARE_STATE", { ...value, time: NowTimeStamp() });
      if (value.state === 1) this.turnPath("/courseware");
    }
  },
  destroyed () {
    this.stopServer();
  }
};



