import { httpGet, basePath } from "./../request";
const Course = `${basePath}/I_CourseService/`;
export default {
    getStudentCourseList: data => {
        return httpGet(
            `${Course}GetDatasByStudentID`,
            data
        );
    },
    getList: data => {
        return httpGet(
            `${Course}GetDatas`,
            data
        );
    },
    getAllList: data => {
        return httpGet(
            `${Course}GetDatasByStudentID`,
            data
        );
    }
};


