<template>
  <div id="app">
    <r-loading v-show="$store.getters.requestNum > 0 && !($route.meta && $route.meta.noLoading) "></r-loading>
    <!-- <keep-alive :max="10" :include="$store.getters.cacheComponents">
      <transition :name="transition">
        <router-view  class="router-view" v-if="$route.meta.cache"></router-view>
      </transition>
    </keep-alive> -->
    <!-- <span>{{transition}}</span> -->
    <transition :name="transition">
      <router-view class="router-view"></router-view>
    </transition>
    <r-image-preview></r-image-preview>
  </div>
</template>

<script>
import RLoading from "./components/r-loading";
import RImagePreview from "./components/r-image/r-image-preview";
import interflow from "./mixin/interflow";
import note from "./mixin/note";
import { FuncType, sendToAPP } from "@/utils/interFlow";
import { LimitType } from "@/utils/enum";

export default {
  name: "App",
  components: { RLoading, RImagePreview },
  mixins: [interflow, note],
  data () {
    return {
      clientHeight: document.documentElement.clientHeight,
      timeStamp: 0
    };
  },
  computed: {
    transition () {
      if (!this.$store.routerDirection) return 'slide-normal'
      return `slide-${this.$store.routerDirection}`
    }
  },
  mounted () {
    // 如果支持 popstate 一般移动端都支持了
    if (window.history && window.history.pushState) {
      // 往历史记录里面添加一条新的当前页面的url
      history.pushState(null, null, document.URL);
      // 给 popstate 绑定一个方法 监听页面刷新
      window.addEventListener("popstate", this.backChange, false); //false阻止默认事件
      this.$on("hook:destroyed", () => {
        window.removeEventListener("popstate", this.backChange, false);
      });
    }
  },
  methods: {
    backChange (e) {
      const isTop =
        this.$route.meta &&
        this.$route.meta.limits &&
        this.$route.meta.limits.indexOf(LimitType.top) > -1;
      const backUrl = this.$route.meta ? this.$route.meta.from : "";
      // 顶级页面连续点击两次返回，则退出应用
      if (isTop) {
        console.log("当前是顶级了，无法再后退");
        window.history.go(1);
        if (e.timeStamp - this.timeStamp < 50) {
          sendToAPP(FuncType.closeAPP, "");
        }
        this.timeStamp = e.timeStamp;
      } else if (backUrl) { // 指定返回页
        this.$router.replace(backUrl);
      } else {
        let len = this.$store.getters.navigations.length;
        if (len > 1 && this.$store.getters.navigations[len - 2].path !== "/login") {
          const { path, params, query } = this.$store.getters.navigations[len - 2];
          this.$router.replace({
            path,
            params,
            query: {
              ...query,
              APP_KEY: Math.random().toString(16).substring(2)
            }
          });
        }
      }
    }
  }
};
</script>

<style lang="less">
@import "../src/styles/index.less";

.router-view {
  // position: absolute;
  // z-index: 999;
  // left: 0;
  // top: 0;
  // width: 100%;
  // height: 100%;
  // transition: all 500ms cubic-bezier(0.55, 0, 0.1, 1);
  // backface-visibility: hidden;
  // background-color: #f0f0f0;
  width: 100%;
  top: 0;
}

.slide-forward-enter-active,
.slide-forward-leave-active,
.slide-normal-enter-active,
.slide-normal-leave-active {
  will-change: transform;
  transition: all 500ms;
  height: 100%;
  top: 0;
  position: absolute;
  backface-visibility: hidden;
  perspective: 1000;
}

/* 路由切换动画 */
.slide-forward-enter,
.slide-back-leave-active {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}

.slide-forward-leave-active,
.slide-back-enter {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}

.slide-normal {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

.slide-normal-leave-active {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
</style>



