  // roomSignalRPort: state => state.common.roomSignalRPort, // 教室signalR服务端口
  deviceID: state => state.common.deviceID,
  localIP: state => state.common.localIP,

  serverConfig: state => state.common.serverConfig,
  interactServer: state => state.common.interactServer, // 教室服务ip
  interactPort: state => state.common.interactPort, // 教室signalR服务端口

  // 统一登录
