/* eslint-disable */
export function formatDate(Dtime, format) {
  if (Dtime) {
    var date = Dtime;
    if (/(y+)/.test("yyyy-MM-dd hh:mm:ss")) {
      try {
        format = format.replace(
          RegExp.$1,
          (date.getFullYear() + "").substr(4 - RegExp.$1.length)
        );
      } catch (e) {
        date = new Date(parseInt(Dtime.slice(6, 19)));
        if (!date || date.toUTCString() == "Invalid Date") {
          return "";
        }
      }
    } else {
      date = new Date(parseInt(Dtime.slice(6, 19)));
      if (!date || date.toUTCString() == "Invalid Date") {
        return "";
      }
    }
    var map = {
      M: date.getMonth() + 1, //月份
      d: date.getDate(), //日
      h: date.getHours(), //小时
      m: date.getMinutes(), //分
      s: date.getSeconds(), //秒
      q: Math.floor((date.getMonth() + 3) / 3), //季度
      S: date.getMilliseconds() //毫秒
    };
    format = format.replace(/([yMdhmsqS])+/g, function(all, t) {
      var v = map[t];
      if (v !== undefined) {
        if (all.length > 1) {
          v = "0" + v;
          v = v.substr(v.length - 2);
        }
        return v;
      } else if (t === "y") {
        return (date.getFullYear() + "").substr(4 - all.length);
      }
      return all;
    });
    return format;
  }
}

export const oneDayLong = 86400000; // 一天的毫秒数，重要数据勿修改

/**************************************时间格式化处理************************************/
export function DateFormat(date, fmt) {
  //author: meizz
  var o = {
    "M+": date.getMonth() + 1, //月份
    "d+": date.getDate(), //日
    "h+": date.getHours(), //小时
    "m+": date.getMinutes(), //分
    "s+": date.getSeconds(), //秒
    "q+": Math.floor((date.getMonth() + 3) / 3), //季度
    S: date.getMilliseconds() //毫秒
  };
  if (/(y+)/.test(fmt))
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + "").substr(4 - RegExp.$1.length)
    );
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length)
      );
  return fmt;
}
export function formatDiff(dateBegin, dateEnd) {
  var dateDiff = dateEnd.getTime() - dateBegin.getTime();
  var dayDiff = Math.floor(dateDiff / (24 * 3600 * 1000));
  var leave1 = dateDiff % (24 * 3600 * 1000);
  var hours = Math.floor(leave1 / (3600 * 1000));
  var leave2 = leave1 % (3600 * 1000);
  var minutes = Math.floor(leave2 / (60 * 1000));
  var leave3 = leave2 % (60 * 1000);
  var seconds = Math.round(leave3 / 1000);
  hours = (Array(2).join(0) + hours).slice(-2);
  minutes = (Array(2).join(0) + minutes).slice(-2);
  seconds = (Array(2).join(0) + seconds).slice(-2);
  var res = {
    day: dayDiff,
    h: hours,
    m: minutes,
    s: seconds
  };
  return res;
}



