import Cookies from "js-cookie";

const tokenKey = "UserId";

export function getToken () {
  return Cookies.get(tokenKey);
}

export function setToken (token) {
  return Cookies.set(tokenKey, token);
}

export function removeToken () {
  return Cookies.remove(tokenKey);
}

/**
 * 统一配置名称前缀
 * @param data
 * @return {string}
 */
const setName = (data) => {
  return `S_${data}`;
};

export const storages = {
  deviceID: setName("deviceID"),

  serverConfig: setName("serverConfig"),
  interactServer: setName("interactServer"),
  interactPort: setName("interactPort"),

  singleLoginType: setName("singleLoginType"),
  singleLoginUrl: setName("singleLoginUrl"),
  singleLogOutUrl: setName("singleLogOutUrl"),

  localIP: setName("localIP"),
  userId: setName("userId"),
  student: setName("student"),
  StudentID: setName("StudentID"),
  userName: setName("userName"),
  userPassword: setName("userSecret"),
  serverIP: setName("serverIP"),
  gatewayIP: setName("gatewayIP"),

  course: setName("course"),
  courseID: setName("courseID"),
  nowCourse: setName("nowCourse"),
  class: setName("class"),
  classID: setName("classID"),
  classStudentID: setName("classStudentID"),
  group: setName("group"),
  groupID: setName("groupID"),
  groupIP: setName("groupIP"),
  groupType: setName("groupType"),
  recIP: setName("recIP"),
  agreeUse: setName("agreeUse"),
  routerDirection: setName("routerDirection"),
  navigations: setName("navigations")
};

/**
 * set 存储方法
 * @param {*} key 键
 * @param {*} value 值
 */
export function setLocalStorage (name, value, expires) {
  if (!value) {
    localStorage.removeItem(name);
  } else {
    let params = { name, value, expires };
    if (expires) {
      let data = Object.assign(params, { startTime: new Date().getTime() });
      localStorage[name] = JSON.stringify(data);
    } else {
      let data = Object.assign(params);
      localStorage[name] = JSON.stringify(data);
    }
  }
}

/**
 * get 存储方法
 * @param {*} name 键
 * @param {*} convert 是否转换为json对象，默认false
 */
export function getLocalStorage (name, convert = false) {
  let item = localStorage[name];
  let object;
  try {
    object = JSON.parse(item);
  } catch (err) {
    return item;
  }

  // 如果有startTime的值，说明设置了失效时间
  if (object && object.startTime) {
    let date = new Date().getTime();
    // 如果大于就是过期了，如果小于或等于就还没过期
    if (date - object.startTime > object.expires) {
      localStorage.removeItem(name);
      return false;
    } else {
      return object.value;
    }
  } else {
    return object.value;
  }
}

/**
 * remove 存储方法
 * @param {*} key 键
 */
export function removeLocalStorage (name) {
  localStorage.removeItem(name);
}

/**
 *
 * @param {*} name
 * @param {*} value
 */
export function setSession (name, value, expires) {
  if (!value) {
    sessionStorage.removeItem(name);
  } else {
    let params = { name, value, expires };
    if (expires) {
      let data = Object.assign(params, { startTime: new Date().getTime() });
      sessionStorage[name] = JSON.stringify(data);
    } else {
      let data = Object.assign(params);
      sessionStorage[name] = JSON.stringify(data);
    }
  }
}

/**
 *
 * @param {*} name
 * @param {*} convert 是否转换为json对象，默认false
 */
export function getSession (name, convert = false) {
  let item = sessionStorage[name];
  let object;
  try {
    object = JSON.parse(item);
  } catch (err) {
    return item;
  }

  // 如果有startTime的值，说明设置了失效时间
  if (object && object.startTime) {
    let date = new Date().getTime();
    // 如果大于就是过期了，如果小于或等于就还没过期
    if (date - object.startTime > object.expires) {
      sessionStorage.removeItem(name);
      return false;
    } else {
      return object.value;
    }
  } else {
    return object.value;
  }
}

/**
 *
 * @param {*} name
 */
export function removeSession (name) {
  sessionStorage.removeItem(name);
}



