import { httpGet, basePath } from "./../request";
const Student = `${basePath}/StuService/`;
export default {
    getData: data => {
        return httpGet(
            `${Student}GetStudentDetails`,
            data
        );
    },
    getVerifyData: data => {
        return httpGet(
            `${Student}GetStudentByXH`,
            data
        );
    },
    getStudentValidate: data => {
        return httpGet(
            `${Student}ValidateNameAndXH`,
            data
        );
    }
};


