import { httpGet, httpPost, basePath } from "./../request";

const Note = `${basePath}/I_StudentNoteService/`;
export default {
    getList: data => {
        return httpGet(
            `${Note}GetDatasByStudent`,
            data
        );
    },
    getSearchList: data => {
        return httpGet(
            `${Note}GetStudentNoteDatas`,
            data
        );
    },
    postSave: data => {
        return httpPost(
            `${Note}Save`,
            data
        );
    },
    postDelete: data => {
        return httpPost(
            `${Note}Delete`,
            data
        );
    }
};


