import { httpGet, httpPost, basePath } from "./../request";
const ClassPractise = `${basePath}/I_ClassPractiseService/`;
export default {
    /*** 课中部分****/
    getList: data => {
        return httpGet(
            `${ClassPractise}GetPLStatsByCIID`,
            data
        );
    },
    // 根据练习编号和序号获取题目
    getDataByIndex: data => {
        return httpGet(
            `${ClassPractise}GetPSAndPA`,
            data
        );
    },
    // 根据题目编号获取题目
    getDataById: data => {
        return httpGet(
            `${ClassPractise}GetPSData`,
            data
        );
    },
    /*** 非课中部分****/
    getPracticeMental: data => {
        return httpGet(
            `${ClassPractise}GetPLStatsByPLID`,
            data
        );
    },
    getStatistic: data => {
        return httpGet(
            `${ClassPractise}GetPLStatsByCIID`,
            data
        );
    },
    getData: data => {
        return httpGet(
            `${ClassPractise}GetPSAndPA`,
            data
        );
    },

    // 提交答案
    postDataAnswer: data => {
        return httpPost(
            `${ClassPractise}SubmitAnswer`,
            data
        );
    },
    // 提交练习
    postDataPractise: data => {
        return httpPost(
            `${ClassPractise}SubmitPractise`,
            data
        );
    },
    // 提交问卷调查
    postDataSurvey: data => {
        return httpPost(
            `${ClassPractise}SubmitSurvey`,
            data
        );
    }
};


