import { httpGet, httpPost, basePath } from "./../request";

const Courseware = `${basePath}/I_CoursewareService/`;
export default {
    getList: data => {
        return httpGet(
            `${Courseware}GetStudentCCDatas`,
            data
        );
    },
    // 提交练习
    postCoursewareTask: data => {
        return httpPost(
            `${Courseware}AddCoursewareTask`,
            data
        );
    }
};


