import { httpGet, basePath } from "./../request";

const ClassInfo = `${basePath}/I_ClassInfoService/`;
export default {
  dataList: data => {
    return httpGet(`${ClassInfo}GetStudentClassInfoByPage`, data);
  },
  dataInfo: data => {
    return httpGet(`${ClassInfo}GetDatasByCIID`, data);
  },
  getStudentScore: data => {
    return httpGet(`${ClassInfo}GetStudentScore`, data);
  },
  getStudentCourse: data => {
    return httpGet(`${ClassInfo}GetStudentClassInfo`, data);
  }
};



