import Vue from "vue";
import Vuex from "vuex";

import user from "./modules/user";
import interact from "./modules/interact";
import common from "./modules/common";
import getters from "./getters";

Vue.use(Vuex);

const store = new Vuex.Store({
  modules: {
    user,
    interact,
    common
  },
  getters
});

export default store;



