import { httpGet, httpPost, basePath } from "./../request";

const GroupDiscussion = `${basePath}/I_GroupWallService/`;
export default {
  getList: data => {
    return httpGet(
      `${GroupDiscussion}GetStudentGSDatas`,
      data
    );
  },
  getStatistic: data => {
    return httpGet(
      `${GroupDiscussion}GetGSStatsByGSID`,
      data
    );
  },
  getData: data => {
    return httpGet(
      `${GroupDiscussion}GetGSAndGDDatas`,
      data
    );
  },
  getStatisticByGroup: data => {
    return httpGet(
      `${GroupDiscussion}GetGSStatsAndCountByGroupID`,
      data
    );
  },
  getDataInfoByGroup: data => {
    return httpGet(
      `${GroupDiscussion}GetGSAndGDDataByGroupID`,
      data
    );
  },
  postDataSave: data => {
    return httpPost(
      `${GroupDiscussion}SubmitGroupSubject`,
      data
    );
  },
};



