import axios from "axios";
import store from "@/store";
import { Toast } from "vant";
// import config from "./../../static/config.json";
const setting = appConfig;
const service = axios.create();
service.defaults.timeout = 1000 * 5; // 请求超时 时长
service.defaults.headers.common["Access-Platform"] = "APP";

service.interceptors.request.use(
  config => {
    // do something before request is sent
    config.baseURL = `http://${store.getters.serverIP}:${setting.mainServerPort}`;
    if (store.getters.token) {
      config.headers["Access-Token"] = store.getters.token;
    }

    store.commit("SET_REQUEST", 1);
    return config;
  },
  error => {
    console.log("request=>error:", error);
    store.commit("SET_REQUEST", -1);
    return Promise.reject(error);
  }
);

// response interceptor
service.interceptors.response.use(
  response => {
    const res = response.data;
    store.commit("SET_REQUEST", -1);
    let expired =
      response &&
      response.headers &&
      response.headers["access-token"] === "Login Is Expired";
    expired =
      expired ||
      (response &&
        response.statusText &&
        response.statusText.indexOf("expired") > -1);
    if (expired) {
      setTimeout(() => {
        Toast("登录已过期，请重新登录");
        store.dispatch("Logout");
        location.reload(); // 刷新页面
      }, 500);
      return { Result: false, Info: "登录已过期，请重新登录~" };
    } else {
      return res;
    }
  },
  error => {
    console.log("response=>error:", error); // for debug

    let expired =
      error.response &&
      error.response.headers &&
      error.response.headers["access-token"] === "Login Is Expired";
    expired =
      expired ||
      (error.response &&
        error.response.statusText &&
        error.response.statusText.indexOf("expired") > -1);

    if (
      error.code === "ECONNABORTED" &&
      error.message.indexOf("timeout") > -1
    ) {
      Toast("请求超时~");
    } else if (error.message.indexOf("Network Error") > -1) {
      Toast("请求失败");
    } else if (expired) {
      Toast("登录已过期，请重新登录~");
      setTimeout(() => {
        store.dispatch("Logout");
        location.reload(); // 刷新页面
      }, 500);
    }
    store.commit("SET_REQUEST", -1);
    return Promise.reject(error);
  }
);

export default service;

export const mentalUrl = url => {
  return `http://${store.getters.serverIP}:${setting.mainServerPort}/${url}`;
};

export const httpUrl = url => {
  return `http://${store.getters.serverIP}:${setting.mainServerPort}/FJRH.Campus/${url}`;
};

export const httpGet = (url, data, config = {}) => {
  return service({
    url: url,
    method: "get",
    params: data,
    ...config
  });
};

export const httpPost = (url, data, config = {}) => {
  return service({
    url: url,
    method: "post",
    data,
    ...config
  });
};

export const basePath = "/FJRH.Campus";



