import { httpGet, httpPost, basePath } from "./../request";

const User = `${basePath}/UserService/`;
export default {
  getLoginCode: data => {
    const config = {
      headers: {
        "Access-Token": undefined
      },
      timeout: 1000 * 3
    };
    return httpGet(
      `${User}GetValidateCode`,
      data,
      config
    );
  },
  postCodeValidate: (data, ex) => {
    const config = {
      headers: {
        "X-Requested-With": "XMLHttpRequest",
        "Content-Type": "application/json; charset=UTF-8",
        "Access-Token": "",
        "Ex-": ex
      },
    };
    return httpPost(
      `${User}ValidateCode`,
      data,
      config
    );
  },
  getUserInfo: data => {
    const config = {
      headers: {
        "X-Requested-With": "XMLHttpRequest",
        "Content-Type": "application/json; charset=UTF-8",
        "Access-Token": ""
      }
    };
    return httpGet(`${User}GetDataHDByUserID`, data, config);
  },
  postLogin: (data, ex) => {
    const config = {
      headers: {
        "X-Requested-With": "XMLHttpRequest",
        "Content-Type": "application/json; charset=UTF-8",
        "Access-Token": ""
      }
    };
    if (ex) config.headers["Ex-"] = ex;
    return httpPost(
      `${User}Login`,
      data,
      config
    );
  },
  postRegister: data => {
    return httpPost(
      `${User}BatchSave`,
      data
    );
  },
  UploadPhoto: data => {
    return httpPost(`${User}/UploadPhoto`, data, { headers: { "Content-Type": "multipart/form-data" } });
  }
};



